.hero-bg-1, .hero-bg-2{
    position: relative;
    display: block;
    width: 100%;
}

.hero-bg-1:before{
    position: absolute;
    content: '';
    background: url("../src/assets/img/slider-bg-2.svg")no-repeat center bottom / cover;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
}

.hero-bg-2:before{
    position: absolute;
    content: '';
    background: url("../src/assets/img/slider-img-1.jpg")no-repeat center bottom / cover;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
}
